<script>
import { MessageBox, Swipe, SwipeItem ,Toast} from "mint-ui";
import axios from "axios";
import Title from "./components/Title";
import Error from "./components/Error";
const host = "http://47.96.131.83:1091";
export default {
  name: "Home",
  data() {
    return {
      dianzan: false,
      checkOptions:[
        {
          value:"1",
          label:"菜品问题",
        },
        {
          value:"2",
          label:"服务问题",
        },
        {
          value:"3",
          label:"环境问题",
        },
        {
          value:"4",
          label:"投诉吐槽",
        },
        {
          value:"5",
          label:"其他",
        }

      ],
      formData: {
        userName: "",
        mobile: "",
        address: "",
        city: "",
        suggest: "",
        questionDesc: "",
        complaintType: [],
        pictureUrl:""
      },
      filename:"",
      fileLoading: false,
      rules: {
        userName: false,
        mobile: false,
        address: false,
        city: false,
        suggest: false,
        questionDesc: false,
        complaintType: false,
        file: false
      },
      imageList:['https://pub-files.jinshuju.net/carousel/5da1a084ea111b22c4a680fa1570873478.png?imageView2/1/w/1600/h/600']
    };
  },
  components: {
    Title,
    Error
  },
  computed: {},
  methods: {
    checkRules(name) {
      if (this.formData[name].length) {
        this.rules[name] = false;
        return false;
      }
      this.rules[name] = true;
      return true;
    },
    submit() {
      if (this.checkRules("complaintType")) {
        this.$refs.complaintType.scrollIntoView(true);
        return;
      }
      if (this.checkRules("questionDesc")) {
        this.$refs.question.scrollIntoView(true);
        return;
      }
      if (
        this.checkRules("city") ||
        this.checkRules("address") ||
        this.checkRules("userName") ||
        this.checkRules("mobile")
      ) {
        return;
      }
      let that = this;
      let url = host + "/api/back/add";
      let { complaintType } = this.formData;
      let data = Object.assign({},this.formData,{complaintType:complaintType.join(',')});
      axios
        .post(url, data, {
          headers: { "Content-Type": "application/json" },
          timeout: 5000,
        })
        .then(function(res) {
          if (res && res.status === 200 && res.data && res.data.code === 0) {
            Toast({
              message: '提交成功',
              iconClass: 'icon icon-success'
            });
            that.formData = Object.assign({},that.$options.data().formData);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    uploadFile() {
      let max_size = 1024 * 1024 * 200;
      let file = this.$refs.file.files[0];
      this.rules.file = false;
      if (file.size > max_size) {
        this.rules.file = true;
        return;
      }
      let that = this;
      let formData = new FormData();
      let url = host + "/api/common/upload";
      formData.append("file", file);
      axios
        .post(url, formData)
        .then(function(res) {
          if (res && res.status === 200 && res.data && res.data.code === 0) {
            that.formData.pictureUrl = res.data.url;
            that.filename = res.data.fileName;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
      
    },
    cancelFile(){
      this.formData.pictureUrl = '';
    }
  }
};
</script>

<style scoped lang="stylus">
.home-wrap {
  font-size: 14px;
  max-width: 700px;
  color: #333;
  margin: auto;
