        line-height: 20px;
        color: #fff;
        text-align: center;
        background-color: #4db8ff;
        -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
        box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
        -webkit-transition: width 0.6s ease;
        transition: width 0.6s ease;
      }

      .finish-file {
        display: flex;
        justify-content: space-around;
        align-items :center;
        width: 100%;
        height: 40px;
         border: 1px dotted #26a2ff;
        a {
          display: inline-block;
          width: 80%;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
        }
      }
    }

    .submit-boxx {
      margin-top: 1.5rem;

      .buttom {
        height: 40px;
        width: 100%;
        line-height: 40px;
        color: rgb(255, 255, 255);
        background: rgb(28, 37, 48);
        text-align: center;
      }

      .tips {
        color: #6b6b6b;
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        margin-top: 10px;

        .active {
          color: rgb(255, 169, 66);
        }

        .icon {
          position: relative;
          top: 2px;
        }
      }
    }
  }

  footer {
    margin-top: 10px;
    text-align: center;
    font-size: 16px;
    color: #333;
  }
}

input, textarea {
  border: 1px solid #d9d9d9;
  width: 100%;
  font-size: 14px;
  text-indent: 8px;
  padding: 8px;
  border-radius: 5pxx;
}

input {
  height: 40px;
}

textarea {
  height: 75px;
}

input:focus, textarea:focus {
  border-color: #26a2ff;
}
</style>




